<style>
  @import "../../assets/orderDetail.css";
  .m-dark {
    background-color: #303133 !important;
    color: #FFF !important;
    word-spacing: space-between;
  }
  .sb-2 {
    display: flex; 
    justify-content: space-between;
  }
  .order-detail-icon {
    font-size: 28px !important;
  }
  .sf-sn-wrap {
    display: flex;
    flex-wrap: nowrap;
    flex-direction: row;
  }
  .click-sn-txt {
    cursor: pointer;
  }
  .click-sn-txt:hover {
    color: #3080fe !important;
  }
  .o-f-a {
    overflow-y: auto;
    max-height: 300px;
    width: 100%;
    padding: 0;
  }
  .o-f-a>ul {
    padding-left: 60px;
  }
  .o-f-a .route-status-txt {
    position: absolute;
    left: -60px;
  }
</style>

<template>
  <div class="o-detail-wraper" v-loading="loading">
    <div class="l-con">
      <!-- 详情信息第一条 -->
      <div class="lc-msg-st">
        <p class="p-name">{{orderMesg.platformName}}</p>
        <p class="p-ot-msg">
          <span>{{orderMesg.orderTypeName || ''}}</span>
          <span style="padding:0 2px;" v-if="orderMesg.orderType !== 4">|</span>
          <span v-if="orderMesg.orderType !== 4">{{orderMesg.reciveMesg}}</span>
          <span style="padding:0 2px;">|</span>
          <span>下单时间{{orderMesg.orderCreateTime}}</span>
          <span style="padding:0 2px;">|</span>
          <span>订单号<el-tag size="mini" v-if="orderMesg.parentOrderNo">子单</el-tag>{{orderMesg.orderId}}</span>
          <!-- 暂时不开放查看母单功能 -->
          <!-- <router-link v-if="orderMesg.parentOrderNo" :to="orderMesg.redirectUrl" target="_blank">查看母单</router-link> -->
        </p>
        <p class="o-status">
          {{orderMesg.orderStatusName}}
        </p>
      </div>
      
      <!-- 详情信息第二条 客户 -->
      <div class="lc-msg-2p1i">
        <div class="icon-w">
          <span class="iconfont icon-user-1 order-detail-icon" aria-hidden="true"></span>
        </div>
        <div class="msg-2p">
          <p>
            <span>{{orderMesg.rName}}</span>
            <span>{{orderMesg.rTel}}</span>
            &nbsp;&nbsp;
            <span v-if="orderMesg.buyerPhone">（会员手机号：{{orderMesg.buyerPhone}}）</span>
          </p>
          <p>{{orderMesg.rAddress}}</p>
        </div>
      </div>

      <!-- 详情信息第三条 店铺地址 -->
      <div class="lc-msg-2p1i">
        <div class="icon-w">
          <span class="iconfont icon-mendian order-detail-icon" aria-hidden="true"></span>
        </div>
        <div class="msg-2p">
          <p>
            <span>{{orderMesg.erpStoreNo}} {{orderMesg.erpStoreName}}</span>
            &nbsp;&nbsp;
            <span v-if="orderMesg.storeLeaderId">（所属团长店：{{orderMesg.storeLeaderId}}-{{orderMesg.virtualStoreName}}-{{orderMesg.userName}}）</span>
          </p>
          <p v-if="orderMesg.orderStatus == 1 || orderMesg.orderStatus == 2">等待门店备货</p>
          <p v-else>{{orderMesg.orderType === 4 ? '无需备货' : '门店已备货'}}</p>
        </div>
      </div>

      <!-- 详情信息第四条 配送 --> 
      <div class="lc-msg-2p1i" v-if="orderMesg.orderType !== 4">
        <div class="icon-w">
          <span class="iconfont icon-kuaidi order-detail-icon" aria-hidden="true"></span>
        </div>
        <div class="msg-2p">
          <p>
            {{orderMesg.expressTypeName}}
          </p>
          <div v-if="orderMesg.storeType == 3" class="sf-sn-wrap">
            <div>物流单号</div>&nbsp;&nbsp;
            <div v-if="orderMesg.deliveryCodes && orderMesg.deliveryCodes.length">
              <p class="click-sn-txt" @click="showLogistics(item)" v-for="item in orderMesg.deliveryCodes" :key="item">{{item}}</p>
            </div>
            <div v-else>
              暂无({{orderMesg.orderStatusName}})
            </div>
          </div>
          <div v-else>
            {{orderMesg.thirdShipStatusName}}
          </div>
        </div>
      </div>


      <!-- 详情信息第五条 商品信息-->
      <div class="lc-msg-2p1i">
        <div class="icon-w">
          <span class="iconfont icon-B-shangpin order-detail-icon" aria-hidden="true"></span>
        </div>
        <div class="msg-2p">
          <p>
            商品信息
          </p>
          <p>
            <span style="color:#e6a23c;padding-right:10px;">订单备注：</span>
            <span>
              {{orderMesg.remark || '暂无'}}
            </span>
          </p>
          
          <!-- 商品列表 -->
          <div class='goods-mesg' v-show="orderMesg.shopOrderItemVoList.length>0">
            <div class="g-m-item" v-for="(item, idx) of orderMesg.shopOrderItemVoList" :key="idx">
              <p>{{item.goodsNo || '-'}} - {{item.productName || '-'}}<span class="supplier-goods" v-if="item.vendorName">【{{item.vendorName}}】</span></p>
              <p>
                <span v-show="item.dispPrice && item.dispPrice != item.payPrice && item.dispPrice != 0 && item.dispPrice != '0.00'">
                  ￥{{item.dispPrice || '0.00'}}
                </span>
              </p>
              <p>￥{{item.payPrice || '0.00'}}</p>
              <p>x{{item.quantity || 0}}</p>
              <p>￥{{item.productTotalAmount || '0.00'}}</p>
            </div>
          </div>
          
          <!-- 合计 -->
          <div class='goods-mesg'>
            <div class="gm-2p">
              <p>商品总计</p>
              <p>￥{{orderMesg.productTotalAmount || '0.00'}}</p>
            </div>
            <div class="gm-2p" v-show="orderMesg.platformId != 5">
              <p>餐盒费</p>
              <p>￥{{orderMesg.boxAmount || '0.00'}}</p>
            </div>
            <div class="gm-2p" v-show="orderMesg.platformId != 5">
              <p>包装费</p>
              <p>￥{{orderMesg.packAmount || '0.00'}}</p>
            </div>
            <div class="gm-2p" v-show="orderMesg.orderType != 3">
              <p>运费</p>
              <p>￥{{orderMesg.deliveryAmount || '0.00'}}</p>
            </div>
            <div class="seller-d-a">
              <p class="sda-tit">
                <span>
                  优惠总金额
                  <el-popover
                    v-if="discountVos.length > 0"
                    placement="right"
                    width="280"
                    popper-class="m-dark"
                    :visible-arrow="false"
                    trigger="hover">
                    <div>
                      <div v-for="(item, idx) in discountVos" :key="idx">
                        <p style="text-align: left;">
                          <span style="word-break: break-all;">
                            {{item.discountName || ''}}
                          </span>
                          <span v-show="item.quantity">   x  {{item.quantity || ''}}</span>
                        </p>
                        <p style="text-align: right;margin-top: 4px;">-￥{{item.discountFee || '0.00'}}</p>
                      </div>
                      <div class="sb-2">
                        <span style="text-align:left;">总计</span>
                        <span style="text-align:right;">-￥{{discountAmount || '0.00'}}</span>
                      </div>
                      <div v-if="orderMesg.platformId != 5">
                        <div style="margin: 10px 0; height:1px; border-bottom: 1px dashed #FFF;"></div>
                        <div class="sb-2">
                          <span style="text-align:left;">商家承担</span>
                          <span style="text-align:right;">￥{{sellerDiscountAmount || '0.00'}}</span>
                        </div>
                        <div class="sb-2">
                          <span style="text-align:left;">平台补贴</span>
                          <span style="text-align:right;">￥{{platformAllowanceAmount || '0.00'}}</span>
                        </div>
                      </div>
                    </div>
                    <i class="el-icon-question" slot="reference" style="cursor: pointer;font-size: 16px;"></i>
                  </el-popover>
                </span>
                
                <span>-￥{{orderMesg.discountAmount || '0.00'}}</span>
              </p>
              <!-- <div class="sda-detail" v-show="orderMesg.platformId !=5 && orderMesg.orderType != 3">
                <p class="sda-det-item">
                  <span>商家承担</span>
                  <span>￥{{orderMesg.sellerDiscountAmount || '0.00'}}</span>
                </p>
                <p class="sda-det-item">
                  <span>平台补助</span>
                  <span>￥{{orderMesg.platformAllowanceAmount || '0.00'}}</span>
                </p>
              </div> -->
            </div>
            <div class="gm-2p" v-show="orderMesg.platformId !=5 && orderMesg.orderType != 3">
              <p>平台服务费</p>
              <p>-￥{{orderMesg.commisionAmount || '0.00'}}</p>
            </div>
            <div class="gm-2p" v-show="orderMesg.platformId != 5 && orderMesg.orderType != 3">
              <p>远距离配送费</p>
              <p>-￥{{orderMesg.sellerDistanceAmount || '0.00'}}</p>
            </div>
            <div class="gm-2p">
              <p>积分奖励</p>
              <p>{{orderMesg.pointAmount || 0}}分</p>
            </div>
          </div>
        </div>
      </div>
      
      <!-- 详情信息第六条 收益 -->
      <div class="lc-msg-2p1i">
        <div class="icon-w">
          <span class="iconfont icon-shouru order-detail-icon" aria-hidden="true"></span>
        </div>
        <div class="msg-2p">
          <p class="gm-2p">
            <span>本单预计收入</span>
            <span class="pr10">￥{{orderMesg.settleAmount || '0.00'}}</span>
          </p>
          <p>
            <span>本单顾客实际支付</span>
            <span class="pl10">￥{{orderMesg.realPaidAmount || '0.00'}}</span>
          </p>
        </div>
      </div>
      
      <!-- 第七条 退款信息 -->
      <div class="lc-msg-2p1i" v-if="orderMesg.shopRefundVos.length > 0">
        <div class="icon-w">
          <span class="iconfont icon-tui order-detail-icon" aria-hidden="true"></span>
        </div>
        <div class="msg-2p">
          <p>
            退款信息
          </p>
          <div class="od-refund-mesg-wrap" v-for="(item, idx) in orderMesg.shopRefundVos" :key="idx">
            <p style="color: #444; line-height: 30px;">
             {{item.refundUser || '--'}} {{item.refundTypeStr || '--'}}（{{item.refundTimeStr || '--'}}）
            </p>
            <p>
              <span class="color-444">退款金额：</span>
              <span class="od-refund-price">￥{{item.refundAmount || '--'}}</span>
            </p>
            <div class="od-refund-goods-mesg-wrap">
              <div class="color-444">
                退款商品：
              </div>
              <div class="od-refund-goods-mesg-list">
                <!-- 商品列表 -->
                <div class="g-m-item" v-for="(jItem, idx) of item.refundItemVoList" :key="idx">
                  <p>{{jItem.goodsNo || '-'}} - {{jItem.goodsName || '-'}}<span class="supplier-goods" v-if="jItem.vendorName">【{{jItem.vendorName}}】</span></p>
                  <p>x{{jItem.refundQuantity || 0}}</p>
                  <p>￥{{jItem.refundAmount || '0.00'}}</p>
                </div>
              </div>
            </div>
            <p>
              <span class="color-444">退款原因：</span>
              <span style="color: #666;">{{item.refundMsg || '--'}}</span>
            </p>
          </div>
        </div>
      </div>

      <!-- 第八条 发票信息 2020-05-25 -->
      <div class="lc-msg-2p1i" v-if="invoiceMsg && invoiceMsg.invoiceCreateTime">
        <div class="icon-w">
          <span class="iconfont icon-querenfapiao order-detail-icon" aria-hidden="true"></span>
        </div>
        <div class="msg-2p">
          <p class="gm-2p">
            <span>发票信息</span>
            <span class="pr10">
              <!-- 开票状态：0-未开票，1-开票中，2-已开票，3-开票失败，4-发票已红冲 -->
              {{
                invoiceMsg.invoiceStatus === 1 ? '开票中' : 
                invoiceMsg.invoiceStatus === 2 ? '已开票' : 
                invoiceMsg.invoiceStatus === 3 ? '开票失败' : 
                invoiceMsg.invoiceStatus === 4 ? '发票已红冲' : '未开票'
              }}
            </span>
          </p>
          <p>
            <span>申请时间:</span>
            <span class="pl10">{{invoiceMsg.invoiceCreateTime}}</span>
          </p>
          <!-- 开票成功 -->
          <p v-if="invoiceMsg.invoiceStatus === 2">
            <span>开票时间:</span>
            <span class="pl10">{{invoiceMsg.invoiceUpdateTime}}</span>
          </p>
          <!-- 开票失败 -->
          <p v-if="invoiceMsg.invoiceStatus === 3">
            <span>失败时间:</span>
            <span class="pl10">{{invoiceMsg.invoiceUpdateTime}}</span>
          </p>
          <p v-if="invoiceMsg.invoiceStatus === 3">
            <span>失败原因:</span>
            <span class="pl10">{{invoiceMsg.invoiceDescp}}</span>
          </p>
          <!-- 红冲发票 -->
          <p v-if="invoiceMsg.invoiceStatus === 4">
            <span>红冲时间:</span>
            <span class="pl10">{{invoiceMsg.redRushTime}}</span>
          </p>
          <p>
            <span>抬头类型:</span>
            <!-- 发票抬头类型：0-个人；1-公司 -->
            <span class="pl10">{{invoiceMsg.invoiceType === 1 ? '公司' : '个人'}}</span>
          </p>
          <p v-if="invoiceMsg.invoiceType === 0">
            <span>个人名称:</span>
            <span class="pl10">{{invoiceMsg.name}}</span>
          </p>
          <p v-if="invoiceMsg.invoiceType === 1">
            <span>公司名称:</span>
            <span class="pl10">{{invoiceMsg.company}}</span>
          </p>
          <p v-if="invoiceMsg.invoiceType === 1">
            <span>公司税号:</span>
            <span class="pl10">{{invoiceMsg.taxIdNo}}</span>
          </p>
          <p class="gm-2p re-co-lh">
            <span>
              <span>收票邮箱:</span>
              <span class="pl10">{{invoiceMsg.email}}</span>
            </span>
            <!-- 20200528 TMD 年终奖到现在也不发，产品一会儿让放开，一会又让隐藏，扯淡！ -->
            <!-- <el-button class="sup-mini" icon="el-icon-info" @click="redRushInvoice" v-if="invoiceMsg.invoiceStatus === 2">红冲发票</el-button> -->
          </p>
        </div>
      </div>
    </div>
    <div class="r-con">
      <p class="rc-t">订单日志</p>
      <ul>
        <li v-for="(item, index) of orderMesg.shopOrderLogVoList" class="order-log-item-wrap" :key="index">
          <div class="log-cre-t-w">{{item.createTime}}</div>
          <div class="log-mesg-w">
            <div class="log-s-w">
              {{item.logStatusDesc}}
            </div>
            <div class="log-r-w" v-if="item.remark && item.remark != ''">
              原因：{{item.remark || '暂无'}}
            </div>
          </div>
        </li>
        
        <div class="order-log-item-wrap" v-if="orderMesg.syncErpStatus == -1">
          <div class="log-cre-t-w"></div>
          <div class="log-mesg-w">
            <div class="btn-wrap">
              <el-button type="primary" size="mini" @click="showDetailGoodsDialog">编辑</el-button>
              <el-button type="primary" size="mini" @click="doRePushHandel">重新推送</el-button>
            </div>
          </div>
        </div>
        
      </ul>
    </div>

    <div>
      <el-dialog title="请填写正确的ERP编码" :visible.sync="dialogFormVisible" width='500px'>
        <div class="goods-item-wrap" v-for="(item, idx) in goodsItemListMesg" :key="idx">
          <div class="outer-sku-id-wrap">
            <el-input v-model="goodsItemListMesg[idx].outerSkuId" placeholder="请输入ERP编码" size="small"></el-input>
          </div>
          <div class="sku-name-wrap">
            - {{goodsItemListMesg[idx].skuName}}
          </div>
        </div>
        <div slot="footer" class="dialog-footer">
          <el-button type="primary" size="mini" @click="doSaveHandel(2)">保存并推送</el-button>
          <el-button type="primary" size="mini" @click="doSaveHandel(1)">保存</el-button>
          <el-button @click="dialogFormVisible = false" size="mini">取消</el-button>
        </div>
      </el-dialog>
    </div>

    <!-- 物流信息 -->
    <div>
      <el-dialog title="物流信息" :visible.sync="logisticsInfoVisible" width='550px'>
        <div class="r-con o-f-a" v-loading="getSFLogisticsInfoLoading">
          <ul>
            <li v-for="(item, index) of routeDetails" class="order-log-item-wrap" :key="index">
              <span class="route-status-txt">{{item.statusStr}}</span>
              <div class="log-cre-t-w">
                {{item.barScanTime}}
              </div>
              <div class="log-mesg-w">
                <div class="log-s-w">
                  {{item.remark}}
                </div>
              </div>
            </li>
          </ul>
        </div>
        <div slot="footer" class="dialog-footer">
          <el-button @click="logisticsInfoVisible = false" size="mini">关 闭</el-button>
        </div>
      </el-dialog>
    </div>
  </div>
</template>

<script>
import * as utils from '@/utils/utils'
const orderMgtUrl = {
  // 获取订单详情
  orderDetail: '/plateform/a/order/orderDetail',
  orderDetailParent: '/member/mall/order/getOrderDetailsByOrderNo',
  // ERP落单失败重试接口
  sync: '/plateform/a/erp/possale/sync',
  // 订单详情修改
  saveDetail: '/plateform/a/order/saveDetail',
  // 订单优惠详情
  orderDetailDiscount: '/plateform/a/order/orderDetailDiscount',
  // 获取顺丰物流信息
  getSFLogisticsInfo: '/member/sf/logistics/getSFLogisticsInfo',
  // 查询发票信息发票
  queryInvoice: '/member/invoice/queryInvoice',
  // 红冲发票
  redRushInvoice: '/member/invoice/redRushInvoice'
}
export default {
  data() {
    return {
      loading: false,
      dialogFormVisible: false, // 编辑商品弹窗
      // 订单ID
      orderId: '',
      // 订单信息
      orderMesg: {
        receiveTime: '',
        reciveMesg: '',
        shopOrderItemVoList: [],
        // 退款信息
        shopRefundVos: []
      },
      goodsItemListMesg: [], // 修改商品编码
      // 优惠详情
      discountVos: [],
      // 总计 订单优惠金额
      discountAmount: '0.00',
      // 平台补助
      platformAllowanceAmount: '0.00',
      // 商家承担优惠金额
      sellerDiscountAmount: '0.00',
      // 物流信息弹窗
      logisticsInfoVisible: false,
      getSFLogisticsInfoLoading: false,
      // 物流信息
      routeDetails: [],
      // 发票信息
      invoiceMsg: {}
    }
  },
  created() {
    this.orderId = this.$route.params.orderId
    if(this.orderId) {
      this.getOrderDetail()
      this.orderDetailDiscount()
      this.queryInvoice()
    }
  },
  methods: {
    /**
     * [getOrderDetail 获取订单详情]
     * @return {[type]} [description]
     */
    getOrderDetail() {
      // 全局展示loading
      utils.showLoading()
      // let reqUrl = ''
      // let params = {}
      // if(this.$route.query.origin === 'p') {
      //   reqUrl = orderMgtUrl.orderDetailParent
      //   params.orderNo = this.orderId 
      // }else {
      //   reqUrl = orderMgtUrl.orderDetail
      //   params.orderId = this.orderId 
      // } 
      this.$http.post(orderMgtUrl.orderDetail, { orderId: this.orderId, DIFFCITY: true }).then(res => {
        if(res.code === 0) {
          // 清除全局loading
          utils.clearLoading()
          if(res.data) {
            // 待自提：等待顾客自提
            // 已自提: 顾客已自提
            if(res.data.orderStatus === 5 && res.data.expressType === 2) {
              res.data.thirdShipStatusName = '等待顾客自提'
            }else if(res.data.orderStatus === 6 && res.data.expressType === 2) {
              res.data.thirdShipStatusName = '顾客已自提'
            }else {
              if(res.data.shipperName && res.data.shipperPhone) {
                res.data.thirdShipStatusName = res.data.shipperName + "\t" + res.data.shipperPhone
              }else {
                if(res.data.orderStatus === 1 || res.data.orderStatus === 2 || res.data.orderStatus === 3) {
                  res.data.thirdShipStatusName = "等待分配骑手"
                }else {
                  if(res.data.shipperPhone){
                    res.data.thirdShipStatusName = "骑手\t" + res.data.shipperPhone
                  }else {
                    res.data.thirdShipStatusName = "暂时无法获取骑手信息"
                  } 
                }
              }

            }
            if(res.data.receiveTime) {
              res.data.receiveTime = utils.timestampToTimeYMDHM(new Date(res.data.receiveTime).getTime())
            }
            if(res.data.orderCreateTime) {
              res.data.orderCreateTime = utils.timestampToTimeYMDHM(new Date(res.data.orderCreateTime).getTime())
            }
            // 自提时间范围
            if(res.data.pickDate) {
              res.data.pickDate = utils.timestampToTimeNoHMS(new Date(res.data.pickDate).getTime())
            }
            if(res.data.pickEndTime) {
              res.data.pickEndTime = utils.timestampToTimeHM(new Date(res.data.pickEndTime).getTime())
            }
            if(res.data.pickStartTime) {
              res.data.pickStartTime = utils.timestampToTimeHM(new Date(res.data.pickStartTime).getTime())
            }

            if(res.data.platformId === 5 && res.data.orderType === 3) {
              res.data.reciveMesg = `要求${res.data.pickDate} ${res.data.pickStartTime}~${res.data.pickEndTime}自提`
            }else if(res.data.expressType === 2) {
              res.data.reciveMesg = `要求${res.data.receiveTime} 自提`
            }else {
              res.data.reciveMesg = `要求${res.data.receiveTime} 送达`
            }

            if(res.data.expressType === 0) {
              if(res.data.platformId === 5) {
                //logisticsCode 0 是自配送，1美团，2达达
                if(res.data.logisticsCode == 1001) {
                  res.data.expressTypeName = "门店自配送"
                }else if(res.data.logisticsCode == 1002) {
                  res.data.expressTypeName = "美团配送"
                }else if(res.data.logisticsCode == 1003) {
                  res.data.expressTypeName = "达达配送"
                }
              }else {
                res.data.expressTypeName = "三方平台配送"
              }
            }else if(res.data.expressType === 1) {
              res.data.expressTypeName = "商家自配送"
            }else if(res.data.expressType === 2) {
              res.data.expressTypeName = "自提订单无需配送"
            }else if(res.data.storeType && res.data.storeType == 3) {
              res.data.expressTypeName = res.data.logisticsName || '顺丰快递'
            }

            res.data.productTotalAmount = utils.moneyToFixed(res.data.productTotalAmount)
            res.data.deliveryAmount = utils.moneyToFixed(res.data.deliveryAmount)
            res.data.totalActivityAmount = utils.moneyToFixed(res.data.totalActivityAmount)
            res.data.discountAmount = utils.moneyToFixed(res.data.discountAmount)
            res.data.sellerDiscountAmount = utils.moneyToFixed(res.data.sellerDiscountAmount)
            res.data.platformAllowanceAmount = utils.moneyToFixed(res.data.platformAllowanceAmount)
            res.data.boxAmount = utils.moneyToFixed(res.data.boxAmount)
            res.data.packAmount = utils.moneyToFixed(res.data.packAmount)
            res.data.sellerDistanceAmount = utils.moneyToFixed(res.data.sellerDistanceAmount)

            res.data.commisionAmount = utils.moneyToFixed(res.data.commisionAmount)

            res.data.settleAmount = utils.moneyToFixed(res.data.settleAmount)
            res.data.realPaidAmount = utils.moneyToFixed(res.data.realPaidAmount)

            let goodsItemListMesgArr = []
            for(let item of res.data.shopOrderItemVoList) {
              // item.dispPrice = 0.00
              item.dispPrice = utils.moneyToFixed(item.dispPrice)
              item.payPrice = utils.moneyToFixed(item.payPrice)
              item.productTotalAmount = utils.moneyToFixed(item.productTotalAmount)

              let _val = {
                id: item.id,
                skuName: item.productName,
                outerSkuId: item.productId
              }
              goodsItemListMesgArr.push(_val)
            }

            if(!res.data.shopRefundVos) {
              res.data.shopRefundVos = []
            }else {
              for(let item of res.data.shopRefundVos) {
                item.refundTimeStr = item.refundTime ? utils.timestampToTimeYMDHM(item.refundTime) : '-'
              }
            }

            this.goodsItemListMesg = goodsItemListMesgArr
            if(res.data.parentOrderNo) {
              res.data.redirectUrl = `/order_mgt/detail/${res.data.parentOrderNo}?origin=p`
            }
            this.orderMesg = res.data
          }
          return
        }
        throw new Error(res.msg)
      }).catch(e => {
        // 清除全局loading
        utils.clearLoading()
        this.$notify.error({
          title: '提示',
          message: e.message
        })
      })
    },
    /**
     * [orderDetailDiscount 订单优惠详情]
     * @return {[type]} [description]
     */
    orderDetailDiscount() {
      this.$http.post(orderMgtUrl.orderDetailDiscount, { orderId: this.orderId, DIFFCITY: true }).then(res => {
        if(res.code === 0) {
          if(res.data) {
            if(res.data.discountVos && res.data.discountVos.length) {
              let discountVosTemp = res.data.discountVos.filter(item => {
                return item
              })
              for(let item of discountVosTemp) {
                item.discountFee = utils.moneyToFixed(item.discountFee)
              }
              this.discountVos = discountVosTemp
            }else {
              this.discountVos = []
            }
            this.discountAmount = res.data.discountAmount ? utils.moneyToFixed(res.data.discountAmount) : '0.00'
            this.platformAllowanceAmount = res.data.platformAllowanceAmount ? utils.moneyToFixed(res.data.platformAllowanceAmount) : '0.00'
            this.sellerDiscountAmount = res.data.sellerDiscountAmount ? utils.moneyToFixed(res.data.sellerDiscountAmount) : '0.00'
          }
          return
        }
        throw new Error(res.msg)
      }).catch(e => {
        console.log("提示(订单优惠详情):", e.message)
      })
    },
    /**
     * @description: 查询发票信息发票
     */
    queryInvoice() {
      this.$http.post(orderMgtUrl.queryInvoice, { orderNo: this.orderId, DIFFCITY: true }).then(res => {
        if(res.code === 0) {
          this.invoiceMsg = { ...res.data } || {}
          return
        }
        throw new Error(res.msg)
      }).catch(e => {
        console.log("提示(查询发票信息发票):", e.message)
      })
    },
    /**
     * [showDetailGoodsDialog 显示编辑商品dialog]
     * @return {[type]} [description]
     */
    showDetailGoodsDialog() {
      this.dialogFormVisible = true
    },
    /**
     * [doSaveHandel 保存]
     * @param  {[type]} _param [ 1: 点击保存 2: 点击保存并提交]
     * @return {[type]}        [description]
     */
    doSaveHandel(_param) {
      this.$http.post(orderMgtUrl.saveDetail, { orderId: this.orderId, items: this.goodsItemListMesg, DIFFCITY: true }).then(res => {
        utils.clearLoading()
        if(res.code === 0) {
          this.dialogFormVisible = false
          if(_param === 1) {
            this.$message.success("保存成功")
            // 成功之后刷新一下页面
            this.getOrderDetail()
          }else if(_param === 2) {
            // 提交代码
            this.doRePushHandel()
          }
          return
        }
        throw new Error(res.msg)
      }).catch(e => {
        this.$notify.error({
          title: '提示',
          message: e.message
        })
      }) 
    },
    /**
     * [doRePushHandel 重新推送]
     * @return {[type]} [description]
     */
    doRePushHandel() {
      utils.showLoading()
      let params = []
      params.push(this.orderId)
      this.$http.post(orderMgtUrl.sync, { orderIdList: params, DIFFCITY: true }).then(res => {
        utils.clearLoading()
        if(res.code === 0) {
          this.$message.success("订单重新推送成功")
          // 成功之后刷新一下页面
          this.getOrderDetail()
          return
        }
        throw new Error(res.msg)
      }).catch(e => {
        // 清除全局loading
        utils.clearLoading()
        this.$notify.error({
          title: '提示',
          message: e.message
        })
      }) 
    },
    /**
     * @description: 获取物流信息
     * @param {type} 
     * @return: 
     */
    showLogistics(shippingSn) {
      // opCode   8000已签收   80派送中   50已揽收 其它运送中
      this.logisticsInfoVisible = true
      this.getSFLogisticsInfoLoading = true
      this.$http.post(orderMgtUrl.getSFLogisticsInfo, {shippingSn: shippingSn, DIFFCITY: true}).then(res => {
        this.getSFLogisticsInfoLoading = false
        if(res.code === 0) {
          let routeDetailsArr = res.data && res.data[0] && res.data[0].routeDetails && res.data[0].routeDetails.length ? res.data[0].routeDetails : []
          this.routeDetails = routeDetailsArr.reverse().map(item => {
            return {
              barScanTime: item.barScanTime,
              opCode: item.opCode,
              remark: item.remark,
              statusStr: item.opCode == "8000" ? '已签收' : (item.opCode == "80" ? '派送中' : (item.opCode == "50" ? '已揽件' : (item.opCode == "54" ? '已发货' : '运送中')))
            }
          })
          return
        }
        throw new Error(res.msg)
      }).catch(e => {
        this.getSFLogisticsInfoLoading = false
        this.$notify.error({
          title: '提示',
          message: e.message
        })
      })
    },
    /**
     * @description: 红冲发票
     */
    redRushInvoice() {
      this.$confirm('您确定要红冲发票吗?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.$http.post(orderMgtUrl.redRushInvoice, { orderNo: this.orderId, DIFFCITY: true }).then(res => {
          if(res.code === 0) {
            this.$message.success("红冲发票成功")
            this.queryInvoice()
            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })
      }).catch(() => {})
    }
  },
  /**
   * [beforeRouteLeave 页面离开]
   * @param  {[type]}   to   [description]
   * @param  {[type]}   from [description]
   * @param  {Function} next [description]
   * @return {[type]}        [description]
   */
  beforeRouteLeave (to, from, next){
    to.meta.keepAlive = true;
    next();
  }
  
}
</script>



